.player {
    width: 280px;
}

.player .player-header {
    background-color: #F0F0F0;
    border-radius: 10px 10px 0px 0px;
}

.player img {
    width: 100%;
    border-radius: 50%;
    position: relative;
    bottom: -50px;
}

.player .player-footer {
    background: #FFFFFF;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
    border-radius: 0px 0px 10px 10px;
    padding-top: 60px;
    padding-bottom: 10px;
}

.player .player-footer h4 {
    color: #6278F7;
    font-size: 18px;
    line-height: 22px;
}

.player .player-footer h5 {
    color: #372e4d;
    font-size: 14px;
    line-height: 22px;
}

.player .player-footer button {
    background-color: #f24c4e;
    border-radius: 10px;
    font-weight: 700;
    font-size: 12px;
    padding: 6px;
    border: none;
    cursor: pointer;
    color: #FFF;
    margin: 16px 0px;
}